/**/
import sketch from 'sketch';//sketch API

export function getSettings(){
  let Settings = sketch.Settings;//sketch Settings API
  let settings = { //settings
    width: 'vw',
    height: 'vw',
    fontSize: 'px',
    lineHeight: 'px',
    border: 'px',
    boxShadow: 'px',
    textShadow: 'px',
    position: 'vw'
  };

  Object.keys(settings).forEach(function(key){
    let value = settings[key],
        unit = Settings.settingForKey(key);
    if(unit !== value){
      //settings
      settings[key] = unit;
      log(key + ':' + unit);
    }else{
      //
      log(key + "");
    }
     

  });

  return settings;

}
